import { GoogleGenerativeAI } from '@google/generative-ai';

export interface AIPersonality {
  name: string;
  type: string;
  description: string;
  style: string;
}

export interface TaskRequest {
  id: string;
  personality: AIPersonality;
  requestText: string;
  createdAt: Date;
}

export interface TaskResponse {
  taskId: string;
  userResponse: string;
  evaluatedAt: Date;
  feedbackText: string;
  score?: number;
}

export interface ChatMessage {
  sender: 'user' | 'ai';
  content: string;
  timestamp: Date;
  evaluation?: ChatEvaluation;
}

export interface ChatEvaluation {
  score: number;
  passed: boolean;
  feedback: string;
  nextAction: 'continue' | 'complete';
  aiResponse?: string;
  improvementPoints?: string[];
  strengths?: string[];
  detailedAnalysis?: string;
  attemptNumber?: number;
}

export interface EnhancedTask extends TaskRequest {
  aiPersonality: {
    name: string;
    type: string;
    personality: string;
    strictness: number;
    passingScore: number;
    taskStyle: string;
  };
  chatHistory: ChatMessage[];
  isCompleted: boolean;
  totalScore: number;
  attempts: number;
}

class GeminiService {
  private genAI: GoogleGenerativeAI;
  private createTaskUrl: string;
  private createChatTaskUrl: string;

  constructor() {
    const apiKey = process.env.REACT_APP_GEMINI_API_KEY;
    this.createTaskUrl = process.env.REACT_APP_CREATE_TASK_URL || '';
    this.createChatTaskUrl = process.env.REACT_APP_CREATE_CHAT_TASK_URL || '';
    
    console.log(' GeminiService :');
    console.log('- API Key:', apiKey ? '' : '');
    console.log('- Create Task URL:', this.createTaskUrl);
    console.log('- Create Chat Task URL:', this.createChatTaskUrl);
    
    if (!apiKey) {
      throw new Error('Gemini API key is not configured');
    }
    
    if (!this.createChatTaskUrl) {
      throw new Error('Create Chat Task URL is not configured');
    }
    
    this.genAI = new GoogleGenerativeAI(apiKey);
  }

  // Cloud Functions
  async generateTaskWithCloudFunction(): Promise<any> {
    try {
      console.log(' :', this.createChatTaskUrl);
      
      const response = await fetch(this.createChatTaskUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      
      console.log(' :', response.status, response.statusText);
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error(' HTTP:', response.status, errorText);
        throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
      }
      
      const data = await response.json();
      console.log(' :', data);
      
      if (!data.success) {
        throw new Error(': ' + (data.error || 'Unknown error'));
      }
      
      return data.task;
    } catch (error) {
      console.error(' Enhanced task generation error:', error);
      throw error;
    }
  }

  // AI
  async generatePersonality(): Promise<AIPersonality> {
    const model = this.genAI.getGenerativeModel({ model: 'gemini-1.5-flash' });
    
    const prompt = `
AI
AI1

: [AI]
: [/////]
: [AI50]
: [30]


: 
: AI
: AI
: 
`;

    try {
      const result = await model.generateContent(prompt);
      const text = result.response.text();
      
      // 
      const lines = text.split('\n').filter(line => line.trim());
      
      return {
        name: this.extractValue(lines, '') || 'AI',
        type: this.extractValue(lines, '') || 'AI',
        description: this.extractValue(lines, '') || 'AI',
        style: this.extractValue(lines, '') || ''
      };
    } catch (error) {
      console.error('AI:', error);
      // 
      return {
        name: '',
        type: 'AI',
        description: 'AI',
        style: ''
      };
    }
  }

  // 
  async generateTask(personality: AIPersonality): Promise<string> {
    const model = this.genAI.getGenerativeModel({ model: 'gemini-1.5-flash' });
    
    const prompt = `
${personality.name}${personality.type}
: ${personality.description}
: ${personality.style}

Chat1

- 
- 
- 
- 
- 

100

`;

    try {
      const result = await model.generateContent(prompt);
      return result.response.text().trim();
    } catch (error) {
      console.error(':', error);
      return `${personality.name}: `;
    }
  }

  // 
  async evaluateResponse(personality: AIPersonality, taskRequest: string, userResponse: string): Promise<{ feedback: string; score: number }> {
    const model = this.genAI.getGenerativeModel({ model: 'gemini-1.5-flash' });
    
    const prompt = `
${personality.name}${personality.type}
: ${personality.description}
: ${personality.style}


${taskRequest}


${userResponse}


1. 100
2. 200
3. 
4. 


: [0-100]
: []
`;

    try {
      const result = await model.generateContent(prompt);
      const text = result.response.text();
      
      const scoreMatch = text.match(/:\s*(\d+)/);
      const feedbackMatch = text.match(/:\s*(.+)/);
      
      const score = scoreMatch ? parseInt(scoreMatch[1]) : 75;
      const feedback = feedbackMatch ? feedbackMatch[1].trim() : `${personality.name}`;
      
      return { feedback, score };
    } catch (error) {
      console.error(':', error);
      return {
        feedback: `${personality.name}`,
        score: 75
      };
    }
  }

  // 
  async generateChatTask(): Promise<EnhancedTask> {
    try {
      const response = await fetch(this.createChatTaskUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      
      if (!data.success) {
        throw new Error('Failed to generate chat task');
      }

      return {
        id: data.task.id || data.taskId,
        personality: {
          name: data.task.aiPersonality.name,
          type: data.task.aiPersonality.type,
          description: data.task.aiPersonality.personality,
          style: data.task.aiPersonality.taskStyle
        },
        requestText: data.task.request,
        createdAt: new Date(),
        aiPersonality: data.task.aiPersonality,
        chatHistory: [],
        isCompleted: false,
        totalScore: 0,
        attempts: 0
      };
    } catch (error) {
      console.error('Chat task generation error:', error);
      throw new Error('');
    }
  }

  // 
  async evaluateChatResponse(
    taskId: string, 
    userId: string, 
    userResponse: string, 
    chatHistory: ChatMessage[]
  ): Promise<ChatEvaluation> {
    try {
      const response = await fetch(`${this.createTaskUrl}/evaluateChatResponse`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          taskId,
          userId,
          userResponse,
          chatHistory: chatHistory.map(msg => ({
            sender: msg.sender,
            content: msg.content,
            timestamp: msg.timestamp
          }))
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      
      if (!data.success) {
        throw new Error('Failed to evaluate chat response');
      }

      return data.evaluation;
    } catch (error) {
      console.error('Chat evaluation error:', error);
      throw new Error('');
    }
  }

  private extractValue(lines: string[], key: string): string | null {
    const line = lines.find(l => l.includes(key));
    if (line) {
      const parts = line.split(':');
      return parts.length > 1 ? parts[1].trim() : null;
    }
    return null;
  }
}

export const geminiService = new GeminiService();
