import { initializeApp } from 'firebase/app';
import { getFirestore } from 'firebase/firestore';
import { getMessaging, getToken, onMessage, type Messaging } from 'firebase/messaging';
import { getAuth, signInAnonymously, onAuthStateChanged, User } from 'firebase/auth';

const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

console.log(' Firebase');
console.log('- App Name:', app.name);
console.log('- Project ID:', firebaseConfig.projectId);

// Initialize Firebase services
export const db = getFirestore(app);
export const auth = getAuth(app);

console.log(' Firebase ');
console.log('- Firestore:', db.app.name);
console.log('- Auth:', auth.app.name);

// MessagingVAPID
let messaging: Messaging | null = null;
const vapidKey = process.env.REACT_APP_FIREBASE_VAPID_KEY;

if (vapidKey && vapidKey !== 'your_vapid_key' && vapidKey.length > 20) {
  try {
    messaging = getMessaging(app);
    console.log('Firebase Cloud Messaging initialized');
  } catch (error) {
    console.warn('Failed to initialize Firebase Cloud Messaging:', error);
  }
} else {
  console.log('Firebase Cloud Messaging disabled - no valid VAPID key');
}

export { messaging };

// FCM Token
export const requestPermissionAndGetToken = async () => {
  try {
    // MessagingnullVAPID
    if (!messaging) {
      console.log('Firebase Cloud Messaging is not initialized');
      return null;
    }
    
    // VAPID FCM
    const vapidKey = process.env.REACT_APP_FIREBASE_VAPID_KEY;
    if (!vapidKey || vapidKey === 'your_vapid_key' || vapidKey.length < 20) {
      console.log('VAPID FCM');
      return null;
    }
    
    // Notification
    if (!('Notification' in window)) {
      console.log('');
      return null;
    }
    
    const permission = await Notification.requestPermission();
    if (permission === 'granted') {
      const token = await getToken(messaging, {
        vapidKey: vapidKey,
      });
      console.log('FCM Token:', token);
      return token;
    } else {
      console.log('');
      return null;
    }
  } catch (error) {
    console.error('FCM:', error);
    return null;
  }
};

// 
export const onMessageListener = () =>
  new Promise((resolve, reject) => {
    try {
      if (!messaging) {
        reject(new Error('Firebase Cloud Messaging is not initialized'));
        return;
      }
      
      onMessage(messaging, (payload) => {
        console.log(':', payload);
        resolve(payload);
      });
    } catch (error) {
      console.error(':', error);
      reject(error);
    }
  });

// 
export const signInAnonymous = async (): Promise<User | null> => {
  try {
    console.log(' ...');
    
    // Firebase Auth
    if (!auth) {
      console.error(' Firebase Auth ');
      return null;
    }
    
    console.log(' Firebase Auth ');
    console.log(' ID:', auth.app.options.projectId);
    
    // 
    console.log(' signInAnonymously ...');
    const result = await signInAnonymously(auth);
    
    if (result && result.user) {
      console.log(' :', result.user.uid);
      console.log(' :', {
        uid: result.user.uid,
        isAnonymous: result.user.isAnonymous,
        creationTime: result.user.metadata.creationTime
      });
      return result.user;
    } else {
      console.error(' : result  user  null');
      return null;
    }
  } catch (error: any) {
    console.error(' :', {
      code: error.code,
      message: error.message,
      stack: error.stack
    });
    
    // Firebase Auth 
    if (error.code === 'auth/operation-not-allowed') {
      console.error(' Firebase Console ');
    } else if (error.code === 'auth/network-request-failed') {
      console.error(' : ');
    }
    
    return null;
  }
};

// 
export const onAuthStateChange = (callback: (user: User | null) => void) => {
  console.log(' ');
  return onAuthStateChanged(auth, (user) => {
    console.log(' :', user ? `UID: ${user.uid}` : '');
    callback(user);
  });
};

export default app;
