import React, { useState, useEffect } from 'react';
import './App.css';
import { requestPermissionAndGetToken, onMessageListener, signInAnonymous, onAuthStateChange } from './firebase';
import { geminiService, EnhancedTask } from './services/geminiService';
import { firestoreService } from './services/firestoreService';
import TaskList from './components/TaskList';
import TaskDetailChat from './components/TaskDetailChat';
import { Task } from './services/firestoreService';
import { User } from 'firebase/auth';

interface NotificationPayload {
  notification?: {
    title?: string;
    body?: string;
  };
  data?: { [key: string]: string };
}

function App() {
  const [user, setUser] = useState<User | null>(null);
  const [currentView, setCurrentView] = useState<'list' | 'detail'>('list');
  const [selectedTask, setSelectedTask] = useState<Task | EnhancedTask | null>(null);
  const [tasks, setTasks] = useState<(Task | EnhancedTask)[]>([]);
  const [loading, setLoading] = useState(false);
  const [initializing, setInitializing] = useState(true);
  const [initStatus, setInitStatus] = useState<string>('...');
  const [notification, setNotification] = useState<NotificationPayload | null>(null);
  const [isInitializing, setIsInitializing] = useState(false); // 

  useEffect(() => {
    console.log(' App useEffect');
    
    // 
    const unsubscribe = onAuthStateChange((user) => {
      console.log(' :', user ? `ID: ${user.uid}` : '');
      setUser(user);
      if (user) {
        setInitStatus('...');
        initializeApp(user);
      } else {
        // 
        setInitStatus('...');
        handleAnonymousSignIn();
      }
    });

    // 10
    const timeoutId = setTimeout(() => {
      if (initializing) {
        console.warn('  - ');
        setInitStatus(' - ');
        setInitializing(false);
      }
    }, 10000);

    console.log(' App useEffect');
    return () => {
      console.log(' App useEffect ');
      unsubscribe();
      clearTimeout(timeoutId);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // 

  const handleAnonymousSignIn = async () => {
    try {
      console.log(' ...');
      setInitStatus('...');
      const user = await signInAnonymous();
      if (user) {
        console.log(' :', user.uid);
        setInitStatus(' - ...');
      } else {
        console.log(' : null');
        setInitStatus(' - ');
        // ID
        createTestUser();
      }
    } catch (error) {
      console.error(' :', error);
      setInitStatus(' - ');
      // ID
      createTestUser();
    }
  };

  const createTestUser = () => {
    // 
    const testUser = {
      uid: 'test-user-' + Date.now(),
      isAnonymous: true,
      metadata: {
        creationTime: new Date().toISOString(),
        lastSignInTime: new Date().toISOString()
      }
    } as User;
    
    console.log(' :', testUser.uid);
    setUser(testUser);
    initializeAppWithoutAuth(testUser.uid);
  };

  const initializeAppWithoutAuth = async (userId: string) => {
    try {
      setInitializing(true);
      console.log(' :', userId);
      
      setInitStatus('...');
      
      // Firestore
      const testTasks: Task[] = [
        {
          id: 'test-1',
          userId: userId,
          personality: {
            name: '',
            type: '',
            description: '',
            style: ''
          },
          requestText: ': 3',
          evaluated: false,
          createdAt: new Date()
        }
      ];
      
      setTasks(testTasks);
      setInitStatus('');
      console.log(' ');
      
    } catch (error) {
      console.error(' :', error);
      setInitStatus('');
    } finally {
      setInitializing(false);
    }
  };

  const initializeApp = async (user: User) => {
    // 
    if (isInitializing) {
      console.log(' ');
      return;
    }
    
    try {
      setIsInitializing(true);
      setInitializing(true);
      console.log(' :', user.uid);
      
      // FCM
      try {
        console.log(' FCM...');
        setInitStatus('...');
        const token = await requestPermissionAndGetToken();
        console.log(' FCM:', token ? '' : '');
        
        setInitStatus('...');
        if (token) {
          await firestoreService.createUser(user.uid, token);
          await firestoreService.saveFCMToken(user.uid, token);
          console.log(' FCM');
        } else {
          // 
          await firestoreService.createUser(user.uid, '');
          console.log(' FCM');
        }
      } catch (fcmError) {
        console.warn(' FCM:', fcmError);
        setInitStatus('...');
        // FCM
        try {
          await firestoreService.createUser(user.uid, '');
          console.log(' FCM');
        } catch (userError) {
          console.error(' :', userError);
        }
      }
      
      // 
      console.log(' ...');
      setInitStatus('...');
      await loadTasks(user.uid);
      console.log(' ');
      
      // 
      try {
        console.log(' ...');
        setInitStatus('...');
        setupMessageListener(user.uid);
        console.log(' ');
      } catch (listenerError) {
        console.warn(' :', listenerError);
      }
      
      setInitStatus('');
      console.log(' ');
    } catch (error) {
      console.error(' :', error);
      setInitStatus('');
    } finally {
      setInitializing(false);
      setIsInitializing(false);
    }
  };

  const setupMessageListener = (userId: string) => {
    onMessageListener()
      .then((payload: any) => {
        console.log(':', payload);
        setNotification(payload);
        // 
        loadTasks(userId);
      })
      .catch((error) => {
        console.warn(':', error);
      });
  };

  const loadTasks = async (userId: string) => {
    try {
      console.log(' :', userId);
      const userTasks = await firestoreService.getUserTasks(userId);
      console.log(' :', userTasks.length);
      setTasks(userTasks);
    } catch (error) {
      console.error(' :', error);
    }
  };

  const generateNewTask = async () => {
    if (!user) return;
    
    setLoading(true);
    try {
      // 
      console.log(' ...');
      const task = await geminiService.generateTaskWithCloudFunction();
      console.log(' :', task);
      
      // 
      await loadTasks(user.uid);
      
      // taskalert
      const taskTitle = task.request || task.title || '';
      const taskContext = task.context || task.description || '';
      
      alert(`\n\n${task.aiPersonality.name}${task.aiPersonality.type}:\n\n${taskTitle}\n\n${taskContext}`);
      
    } catch (error) {
      console.error(' :', error);
      const errorMessage = error instanceof Error ? error.message : 'Unknown error';
      alert(`: ${errorMessage}\n\n`);
    } finally {
      setLoading(false);
    }
  };

  const openTaskDetail = (task: Task | EnhancedTask) => {
    setSelectedTask(task);
    setCurrentView('detail');
  };

  const closeTaskDetail = () => {
    setCurrentView('list');
    setSelectedTask(null);
    if (user) {
      loadTasks(user.uid); // 
    }
  };

  // : Firebase
  useEffect(() => {
    console.log(' Firebase:');
    console.log('- API Key:', process.env.REACT_APP_FIREBASE_API_KEY ? '' : '');
    console.log('- Project ID:', process.env.REACT_APP_FIREBASE_PROJECT_ID);
    console.log('- Auth Domain:', process.env.REACT_APP_FIREBASE_AUTH_DOMAIN);
    console.log('- VAPID Key:', process.env.REACT_APP_FIREBASE_VAPID_KEY ? '' : '');
  }, []);

  // 
  if (initializing || !user) {
    return (
      <div className="App">
        <header className="app-header">
          <h1> Chat</h1>
          <p>...</p>
        </header>
        <main className="app-main">
          <div className="loading-container">
            <div className="loading-spinner"></div>
            <p>...</p>
            <p className="init-status">{initStatus}</p>
            
            {initStatus.includes('') || initStatus.includes('') ? (
              <div className="error-help">
                <h4> Firebase</h4>
                <ol>
                  <li>
                    <a 
                      href="https://console.firebase.google.com" 
                      target="_blank" 
                      rel="noopener noreferrer"
                    >
                      Firebase Console
                    </a>
                    
                  </li>
                  <li>AuthenticationSign-in method</li>
                  <li></li>
                  <li></li>
                </ol>
                <button 
                  onClick={() => window.location.reload()} 
                  className="retry-btn"
                >
                   
                </button>
              </div>
            ) : (
              <small className="loading-hint">
                
              </small>
            )}
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="App" style={{
      background: '#fccc00',
      minHeight: '100vh',
      color: '#2c3e50'
    }}>
      <header className="app-header" style={{
        backgroundColor: '#eb6100',
        color: 'white',
        borderBottom: '5px solid #004c97',
        padding: '20px'
      }}>
        <div className="header-content">
          <div>
            <h1> Chat</h1>
            <p>AI</p>
          </div>
          {user?.uid.startsWith('test-user-') && (
            <div className="test-mode-badge" style={{
              backgroundColor: '#fccc00',
              color: '#2c3e50',
              border: '2px solid #914e25'
            }}>
               
            </div>
          )}
        </div>
      </header>

      {notification && (
        <div className="notification-banner">
          <h3> </h3>
          <p><strong>{notification.notification?.title}</strong></p>
          <p>{notification.notification?.body}</p>
          <button onClick={() => setNotification(null)}></button>
        </div>
      )}

      <main className="app-main" style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        textAlign: 'center',
        width: '100%',
        minHeight: 'calc(100vh - 120px)'
      }}>
        <div className="center-content" style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          textAlign: 'center',
          width: '100%',
          maxWidth: '800px'
        }}>
        {currentView === 'list' ? (
          <div className="center-list" style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            width: '100%'
          }}>
            <div className="action-section" style={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              textAlign: 'center',
              marginBottom: '32px'
            }}>
              <button 
                onClick={generateNewTask} 
                disabled={loading}
                className="generate-task-btn"
                style={{
                  backgroundColor: '#004c97',
                  color: 'white',
                  border: '3px solid #15722c',
                  padding: '15px 30px',
                  borderRadius: '25px',
                  fontSize: '16px',
                  fontWeight: 'bold'
                }}
              >
                {loading ? ' ...' : ' AI'}
              </button>
            </div>
            
            <TaskList 
              tasks={tasks} 
              onTaskSelect={openTaskDetail}
            />
          </div>
        ) : (
          selectedTask && (
            <TaskDetailChat 
              task={selectedTask} 
              onClose={closeTaskDetail}
              userId={user.uid}
            />
          )
        )}
        </div>
      </main>
    </div>
  );
}

export default App;
